<template>
<div>
        <div style="padding: 20px;">
            <el-button @click="toCreate" type="primary">添加用户</el-button>
        </div>
        <div style="padding-left: 20px;">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="姓名">
                    <el-input   v-model="formInline.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="部门">
                    <el-cascader    collapse-tags   v-model="formInline.depIdList" filterable :props="tableProps" :options="orgList"
                                 :show-all-levels="false"></el-cascader>
                </el-form-item>
                <el-form-item label="状态">
                    <el-select v-model="formInline.status" placeholder="">
                        <el-option label="全部" :value="0"></el-option>
                        <el-option label="在职" :value="1"></el-option>
                        <el-option label="离职" :value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="search">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table
                :data="tableData"
                border
                :row-class-name="tableRowClassName"
                style="width: 100%">

            <el-table-column

                    label="头像"
                    width="80">
                <template slot-scope="scope">
                    <img :src="scope.row.avatarUrl " style="width: 30px;height:30px;"/>
                </template>
            </el-table-column>

            <el-table-column
                    prop="name"
                    label="姓名"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="deptName"
                    label="部门"
                    width="180">
            </el-table-column>

            <el-table-column
                    prop="phone"
                    label="手机号"
                    width="180">
            </el-table-column>

            <el-table-column
                    prop="gender"
                    label="性别"
                    width="50">
                <template slot-scope="scope">
                    {{ scope.row.gender| genderShow }}
                </template>
            </el-table-column>
            <el-table-column
                    prop="gender"
                    label="状态"
                    width="80">
                <template slot-scope="scope">
                    {{ scope.row.leaveDate| statusShow }}
                </template>
            </el-table-column>

            <el-table-column
                    prop="entryDate"
                    label="入职日期"
                    width="180">
            </el-table-column>


            <el-table-column
                    prop="leaveDate"
                    label="离职日期"
                    width="180">
            </el-table-column>
            <el-table-column
            fixed="right"
            width="200"
                    prop="address"
                    label="操作">
                <template slot-scope="scope">
                    <el-button @click="toEdit(scope.row)" type="text" size="small">编辑</el-button>


                    <el-popconfirm v-if="!scope.row.leaveDate" style="margin-left: 10px;" @confirm="leaveOrg(scope.row)"
                                   title="确定要办理离职吗？"
                    >

                        <el-button slot="reference" type="text" size="small">离职</el-button>

                    </el-popconfirm>

                </template>
            </el-table-column>
        </el-table>

        <div style="text-align: center;padding: 20px;">
            <el-pagination
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 50]"
                    :page-size="pageData.count"
                    :current-page="pageData.currentPage"
                    :total="pageData.total">
            </el-pagination>
        </div>

    </div>
</template>

<script>
import {getOrgTreeAll} from '@/api/org'
import { queryUserList,leave} from '@/api/user'

export default {
    name: "index",
    components: {},
    data() {
        return {
            formInline: {
                name: '',
                depIdList: '',
                status: 1
            },
            tableProps: {
                value: 'id',
                label: 'name',
                multiple:true,
                checkStrictly: true
            },
            tableData: [],
            pageData: {
                total: 0,
                count: 10,

                currentPage: 1
            },
            orgList:[]
        }
    },
    filters: {
        statusShow: function (d) {
            if (d == undefined || d == null) {
                return '在职'
            }
            return '离职'
        }
    },
    watch: {},
    mounted() {

        this.getData();
        this.getOrg();
    },
    methods: {
        getOrg() {
            var t = this;
            getOrgTreeAll({ deptId: 0, isDept: true }).then(rsp => {
                t.orgList = rsp.data;
            }).catch(err => {

                this.$message.error(err.response.data)
            })
        },
        search() {
          this.pageData.currentPage=1;
          this.getData();
        },
        tableRowClassName({row, rowIndex}) {

            if (row.leaveDate) {
                return 'leave-row';
            }
            return '';
        },
        toEdit(row){
            this.$router.push("/org/user/edit?id="+row.id)
        },
        leaveOrg(row){
            var t=this;
                leave(row).then(res=>{
                    t.getData();
                })
        },
        toCreate() {
            this.$router.push("/org/user/create")
        },
        handleSizeChange(val) {

            this.pageData.count=val;
            this.pageData.currentPage=1;
            this.getData();
        },
        handleCurrentChange(val) {

            this.pageData.currentPage=val;
            this.getData();
        },

        getData() {

            let depIdList = this.formInline.depIdList;
            var depIdFinalList=[];
            for(var d of depIdList){
                let dElement = d[d.length-1];
                depIdFinalList.push(dElement)
            }
            var t = this;
            queryUserList({
                page: t.pageData.currentPage,
                count: t.pageData.count,
                name: t.formInline.name,
                depIdList: depIdFinalList,
                status: t.formInline.status
            }).then(rsp => {
                t.tableData = rsp.data.records;
                t.pageData.total = rsp.data.total;
            }).catch(err => {

                this.$message.error(err.response.data)
            })
        },
    }
}
</script>

<style scoped>
/deep/ .el-table .leave-row {
    background: lightblue !important;
}
</style>
